.rf-responsive-img {
  width: 100%;
  height: auto; }

.rf-responsive-vid {
  position: relative;
  width: 100%; }
  .rf-responsive-vid::before {
    content: '';
    display: block;
    width: 100%;
    padding-bottom: 56.25%; }
  .rf-responsive-vid--4x3::before {
    padding-bottom: 75%; }
  .rf-responsive-vid--1x1::before {
    padding-bottom: 100%; }
  .rf-responsive-vid__player {
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: 50% 50%;
    top: 0;
    left: 0; }
